import React, { useState } from "react";
import {
  Box,
  Button,
  Grid,
  TextField,
  MenuItem,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import toastMessage from "../../utils/toastMessage";
import {
  verifyBankAccount,
  B2BBankTransfer,
} from "../../api/login/Login";

const TRANSFER_MODES = [
  "IMPS",
  "NEFT",
  "RTGS",
  "BANKTRANSFER",
];

const ACCOUNT_TYPES = ["SAVINGS", "CURRENT"];

const B2BBankTransferPage = () => {
  const [verified, setVerified] = useState(false);
  const [loading, setLoading] = useState(false);
  const [openTpin, setOpenTpin] = useState(false);
  const [tpin, setTpin] = useState("");

  const [form, setForm] = useState({
    transfer_mode: "",
    bank_account_number: "",
    bank_ifsc: "",
    beneficiary_name: "",
    beneficiary_account_name: "",
    beneficiary_account_type: "",
    beneficiary_email: "",
    beneficiary_mobile: "",
    beneficiary_add: "",
    beneficiary_city: "",
    beneficiary_state: "",
    beneficiary_pin_code: "",
    transfer_amount: "",
  });

  const setField = (key, value) => {
    setForm((prev) => ({ ...prev, [key]: value }));
  };

  const verifyAccount = async () => {
    if (!form.bank_account_number || !form.bank_ifsc) {
      toastMessage(true, "Account number and IFSC are required");
      return;
    }

    try {
      setLoading(true);

      const res = await verifyBankAccount({
        accountNumber: form.bank_account_number,
        bankIfsc: form.bank_ifsc,
      });

      if (res?.data?.success) {
        const data = res?.data?.data;

        if (!data?.name) {
          toastMessage(true, "Unable to fetch account holder name");
          return;
        }

        setForm((prev) => ({
          ...prev,
          beneficiary_name: data.name,
          beneficiary_account_name:
            data.ifscDetails?.bankName || "",
        }));

        setVerified(true);
        toastMessage(false, "Account Verified Successfully");
      } else {
        toastMessage(true, res?.data?.message || "Verification failed");
      }
    } catch {
      toastMessage(true, "Verification error");
    } finally {
      setLoading(false);
    }
  };

  const submitPayout = async () => {
    if (!verified) {
      toastMessage(true, "Please verify bank account first");
      return;
    }

    if (
      !["IMPS", "NEFT", "RTGS", "BANKTRANSFER"].includes(
        form.transfer_mode
      )
    ) {
      toastMessage(
        true,
        "transfer_mode must be one of IMPS, NEFT, RTGS, BANKTRANSFER"
      );
      return;
    }

    if (!/^\d{4}$/.test(tpin)) {
      toastMessage(true, "Invalid TPIN");
      return;
    }

    if (!form.transfer_amount || Number(form.transfer_amount) <= 0) {
      toastMessage(true, "Enter valid amount");
      return;
    }

    try {
      setLoading(true);

      const res = await B2BBankTransfer({
        ...form,
        transfer_amount: Number(form.transfer_amount),
        tpin,
      });

      if (res?.data?.success) {
        toastMessage(false, res.data.message || "Payout Successful");
        resetForm();
      } else {
        toastMessage(true, res?.data?.message || "Payout Failed");
      }
    } catch {
      toastMessage(true, "Server error");
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setVerified(false);
    setOpenTpin(false);
    setTpin("");
    setForm({
      transfer_mode: "",
      bank_account_number: "",
      bank_ifsc: "",
      beneficiary_name: "",
      beneficiary_account_name: "",
      beneficiary_account_type: "",
      beneficiary_email: "",
      beneficiary_mobile: "",
      beneficiary_add: "",
      beneficiary_city: "",
      beneficiary_state: "",
      beneficiary_pin_code: "",
      transfer_amount: "",
    });
  };

  return (
    <>
      <Box p={3} bgcolor="#fff" borderRadius={2}>
        <Typography variant="h6" mb={2}>
          B2B Bank Transfer
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={12} md={4}>
            <TextField
              select
              fullWidth
              className="mui-autofill-fix"
              label="Transfer Mode"
              value={form.transfer_mode}
              disabled={loading}
              onChange={(e) =>
                setField("transfer_mode", e.target.value)
              }
            >
              {TRANSFER_MODES.map((mode) => (
                <MenuItem key={mode} value={mode}>
                  {mode}
                </MenuItem>
              ))}
            </TextField>
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              label="Account Number"
              className="mui-autofill-fix"
              value={form.bank_account_number}
              disabled={verified || loading}
              onChange={(e) =>
                setField("bank_account_number", e.target.value)
              }
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              fullWidth
              className="mui-autofill-fix"
              label="IFSC Code"
              value={form.bank_ifsc}
              disabled={verified || loading}
              onChange={(e) =>
                setField("bank_ifsc", e.target.value.toUpperCase())
              }
            />
          </Grid>

          {!verified && (
            <Grid item xs={12}>
              <Button
                variant="outlined"
                onClick={verifyAccount}
                disabled={loading}
              >
                {loading ? "Verifying..." : "Verify Account"}
              </Button>
            </Grid>
          )}

          {verified && (
            <>
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Account Holder Name"
                  className="mui-autofill-fix"
                  value={form.beneficiary_name}
                  disabled
                />
                <Typography variant="caption" color="success.main">
                  ✔ Verified
                </Typography>
              </Grid>

              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Bank Name"
                  className="mui-autofill-fix"
                  value={form.beneficiary_account_name}
                  disabled
                />
              </Grid>

              {[
                ["beneficiary_email", "Email"],
                ["beneficiary_mobile", "Mobile"],
                ["beneficiary_add", "Address"],
                ["beneficiary_city", "City"],
                ["beneficiary_state", "State"],
                ["beneficiary_pin_code", "PIN Code"],
              ].map(([key, label]) => (
                <Grid item xs={12} md={4} key={key}>
                  <TextField
                    fullWidth
                    className="mui-autofill-fix"
                    label={label}
                    value={form[key]}
                    disabled={loading}
                    onChange={(e) =>
                      setField(key, e.target.value)
                    }
                  />
                </Grid>
              ))}

              <Grid item xs={12} md={4}>
                <TextField
                  select
                  fullWidth
                  label="Account Type"
                  className="mui-autofill-fix"
                  value={form.beneficiary_account_type}
                  disabled={loading}
                  onChange={(e) =>
                    setField(
                      "beneficiary_account_type",
                      e.target.value
                    )
                  }
                >
                  {ACCOUNT_TYPES.map((type) => (
                    <MenuItem key={type} value={type}>
                      {type}
                    </MenuItem>
                  ))}
                </TextField>
              </Grid>

              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  type="number"
                  className="mui-autofill-fix"
                  label="Amount"
                  value={form.transfer_amount}
                  disabled={loading}
                  onChange={(e) =>
                    setField("transfer_amount", e.target.value)
                  }
                />
              </Grid>
            </>
          )}
        </Grid>

        {verified && (
          <Box mt={3}>
            <Button
              variant="contained"
              onClick={() => setOpenTpin(true)}
              disabled={loading || !form.transfer_mode}
            >
              Submit
            </Button>
          </Box>
        )}
      </Box>

      <Dialog open={openTpin} onClose={() => setOpenTpin(false)}>
        <DialogTitle align="center">Enter TPIN</DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            type="password"
            className="mui-autofill-fix"
            value={tpin}
            disabled={loading}
            inputProps={{ maxLength: 4 }}
            onChange={(e) =>
              /^\d{0,4}$/.test(e.target.value) &&
              setTpin(e.target.value)
            }
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenTpin(false)} disabled={loading}>
            Cancel
          </Button>
          <Button onClick={submitPayout} disabled={loading}>
            {loading ? "Processing..." : "Confirm"}
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default B2BBankTransferPage;
